$font-stack: 'PTSans', sans-serif;

@font-face {
  font-family: 'PTSans';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/ptsans/ptsans.ttf);
}

@font-face {
  font-family: 'PTSansBold';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/ptsans/ptsansbold.ttf);
}

@font-face {
  font-family: 'PTSansItalic';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/ptsans/ptsansitalic.ttf);
}

@font-face {
  font-family: 'PTSansCaption';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/ptsanscaption/ptsanscaption.ttf);
}

@font-face {
  font-family: 'PTSansCaptionBold';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/ptsanscaption/ptsanscaptionbold.ttf);
}

.none {
  padding: 0;
  margin: 0;
}

html {
  min-width: 1240px !important;
}

.container {
  width: 1240px !important;
}

body {
  font-family: $font-stack;
  min-width: 1240px !important;
  color: #383838;
}

header {
  overflow: hidden;
  padding-bottom: 150px;
  .background__header {
    height: 951px;
    background-image: url(../img/fonPage.png);
    background-size: cover;
    position: relative;
    overflow: hidden;
  }
  h2 {
    color: #fff;
    border: 1px solid #fff;
    border-radius: 50px;
    padding: 11px 26px 14px;
    font-family: PTSans;
    font-size: 26px;
    letter-spacing: -1.01px;
    width: 752px;
    margin: 122px 0 0 33px;
  }
  h1 {
    color: #fff;
    display: block;
    margin: 29px 0 0 37px;
    letter-spacing: -0.5px;
    line-height: 76px;
    padding: 0;
    font-size: 72px;
    font-family: PTSansCaptionBold;
  }
  p {
    color: #fff;
    font-size: 29px;
    margin: 90px 0 0 36px;
    line-height: 41px;
    letter-spacing: 0.4px;
  }
  #laptopImg {
    position: absolute;
    top: 207px;
    right: -204px;
  }

  .bubbles {
    img {
      &:nth-child(1) {
        position: absolute;
        top: 96px;
        left: -162px;
        height: 125px;
      }
      &:nth-child(2) {
        position: absolute;
        top: 313px;
        right: 152px;
        width: 54px;
      }
      &:nth-child(3) {
        position: absolute;
        top: 39px;
        width: 209px;
        right: -395px;
      }
    }
  }
}

.dataCourse {
  .bubbles {
    img {
      &:nth-child(1) {
        position: absolute;
        top: -354px;
        left: -590px;
        height: 440px;
        z-index: 1000;
      }
      &:nth-child(2) {
        position: absolute;
        top: -263px;
        right: -131px;
        width: 180px;
        z-index: 1000;
      }
    }
  }
  .info {
    background-color: #fff;
    border-radius: 12px;
    position: absolute;
    width: 108.35%;
    top: -140px;
    padding: 75px 0 75px 0;
    margin: 0 0 0 -67px;
    font-size: 25px;
    font-family: PTSans;
    -moz-box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    -webkit-box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    table {
      border-collapse: separate;
      border-spacing: 98px 5px;
      vertical-align: top;
      float: left;
      tr {
        th {
          font-family: PTSansCaptionBold;
          text-transform: uppercase;
          letter-spacing: -0.5px;
        }
        td {
          vertical-align: top;
          line-height: 32px;
        }
      }
    }
    .buttonInfo {
      display: inline-block;
      margin: -13px 0 0 33px;
      a {
        display: block;
        background-color: #f19264;
        padding: 17px 47px 13px 43px;
        text-transform: uppercase;
        font-family: PTSansCaptionBold;
        border-radius: 10px;
        letter-spacing: -0.8px;
        color: #fff;
      }
      span {
        font-size: 18px;
        letter-spacing: 0.25px;
        margin: 21px 0 0 0;
        line-height: 22px;
        display: inline-block;
      }
    }
  }
}

.aboutCourse {
  margin-top: 152px;
  margin-bottom: 281px;
  h3 {
    font-size: 70px;
    display: block;
    font-family: PTSansCaptionBold;
    margin: 0 0 72px 115px;
    color: #f19264;
  }
  .text__aboutCourse {
    div {
        p {
          font-size: 24px;
          margin: 0;
          line-height: 32px;
          margin: -6px 0 21px 115px;
          letter-spacing: -0.05px;
        }
        &:nth-child(2) {
          p {
            margin: -7px 0 21px 84px;
          }
        }
    }
  }
}

.background__advantagesCourse {
  background-color: #f1f8fe;
  padding-bottom: 143px;
}
.advantagesCourse {
  padding-top: 167px;
  h3 {
    text-align: center;
    display: block;
    letter-spacing: 1px;
    width: 100%;
    font-size: 70px;
    font-family: PTSansCaptionBold;
    margin-bottom: 106px;
  }
  .items__advantagesCourse {
    div {
      &:nth-child(1) {
        .item__advantagesCourse {
          text-align: center;
          margin-bottom: 82px;
          img {
            margin: 0 0 28px -61px;
            width: 79px;
          }
          h4 {
            font-size: 30px;
            font-family: PTSansCaptionBold;
            margin: 0 0 0 -44px;
          }
          p {
            font-size: 26px;
            font-family: PTSans;
            line-height: 32px;
            letter-spacing: -1px;
            margin: 9px 0 0 -49px;
          }
          &:nth-child(2) {
            img {
              width: 74px;
              margin: 0 0 36px -61px;
            }
          }
        }
      }
      &:nth-child(2) {
        .item__advantagesCourse {
          text-align: center;
          margin-bottom: 82px;
          img {
            margin: -9px -125px 26px -61px;
            width: 70px;
          }
          h4 {
            font-size: 30px;
            font-family: PTSansCaptionBold;
            margin: 0 0 0 45px;
          }
          p {
            font-size: 26px;
            font-family: PTSans;
            line-height: 32px;
            letter-spacing: -1px;
            margin: 11px 0 0 51px;
          }
          &:nth-child(2) {
            img {
              width: 75px;
              margin: -3px 0 36px 62px;
            }
          }
        }
      }
    }
  }
}


.learnCourse {
  margin-top: 146px;
  margin-bottom: 113px;
  h3 {
    font-size: 72px;
    font-family: PTSansCaptionBold;
    letter-spacing: -0.5px;
    margin-bottom: 104px;
    margin-left: 28px;
  }
  .item__learnCourse {
    margin-bottom: 85px;
    margin-left: 28px;
    img {
      margin: 9px 80px 0 -7px;
      vertical-align: top;
    }
    div {
      display: inline-block;
      h4 {
        font-size: 31px;
        font-family: PTSansCaptionBold;
      }
      p {
        font-size: 24px;
        line-height: 33px;
        margin-top: 8px;
      }
    }
    &:nth-child(2) {
      margin-bottom: 57px;
      img {
        margin: 4px 96px 0 5px;
      }
      div {
        h4 {
          margin-top: -3px;
          margin-bottom: 11px;
          letter-spacing: -0.5px;
        }
      }
    }
    &:nth-child(3) {
      margin-bottom: 57px;
      img {
        margin: 9px 92px 0 5px;
      }
      div {
        h4 {
          margin-top: 0px;
          margin-bottom: 11px;
          letter-spacing: -.5px;
        }
        span {
          font-size: 23px;
          font-family: PTSans;
          letter-spacing: 0.5px;
          line-height: 31px;
          margin-top: -2px;
          display: inline-block;
          color: #a6afb7;
        }
      }
    }
  }
}

.background__resultsCourse {
  background-color: #f1f8fe;
  padding-bottom: 198px;
}
.resultsCourse {
  padding-top: 178px;
  h3 {
    font-size: 70px;
    font-family: PTSansCaptionBold;
    letter-spacing: 0.5px;
    margin-bottom: 83px;
    margin-left: 94px;
    color: #f19264;
  }

  h4 {
    font-size: 30px;
    font-family: PTSansCaptionBold;
    margin-bottom: 40px;
    margin-left: 94px;
  }

  .rightPart {
    h4 {
      margin-left: 148px;
    }
    .items__resultsCourse {
      margin-left: 60px;
      .item__resultsCourse:nth-child(2) {
          margin-bottom: 25px;
      }
    }
  }
  .items__resultsCourse {
    .item__resultsCourse {
      margin-bottom: 22px;
      margin-left: 84px;
      img {
        vertical-align: top;
        margin: 0 26px 0 -58px;
      }
      p {
        display: inline-block;
        font-size: 24px;
        line-height: 33px;
        letter-spacing: 0px;
      }
      &:nth-child(2) {
        margin-bottom: 44px;
      }
      &:nth-child(3) {
        margin-bottom: 30px;
      }
      &:nth-child(4) {
        margin-bottom: 30px;
      }
    }
    &:nth-child(2) {

    }
  }
  #diplomaImg {
        margin: 57px 0 0 0;
  }
}


.reviewsCourse {
  margin-top: 140px;
  margin-bottom: 170px;
  h3 {
    font-size: 70px;
    font-family: PTSansCaptionBold;
    text-align: center;
    letter-spacing: 0.5px;
    margin-bottom: 78px;
  }

  #arrowRight {
    cursor: pointer;
    position: absolute;
    top: 48%;
    right: 0;
    z-index: 1000;
  }

  #arrowLeft {
    cursor: pointer;
    position: absolute;
    top: 48%;
    left: 0;
    z-index: 1000;
  }
  .slick-list {
    margin: 0 50px 0 50px;
  }
  .items__sliderCourse {
    padding-top: 75px;
    padding-bottom: 57px;
    background-color: #f1f8fe;
    width: 1260px;
    .photo__sliderCourse {
      width: 25%;
      display: inline-block;
      vertical-align: top;
      text-align: center;
      img {
        margin: 6px 0 33px 0;
        text-align: center;
        display: inline-block;
      }
      span {
        font-size: 22px;
        font-family: PTSansCaptionBold;
        margin: 0;
        letter-spacing: -.5px;
        display: block;
      }
    }
    .review__sliderCourse {
      width: 60%;
      display: inline-block;
      vertical-align: top;
      font-size: 24px;
      line-height: 31px;
      padding-left: 0;
      span {
        margin-bottom: 18px;
        display: block;
        letter-spacing: 0px;
      }
    }
  }
}

.background__priceCourse {
  background-color: #f1f8fe;
  padding-bottom: 196px;
}

.priceCourse {
  margin-top: 170px;
  h3 {
    text-align: center;
    font-size: 70px;
    margin-left: -13px;
    font-family: PTSansCaptionBold;
    letter-spacing: 0.5px;
    margin-bottom: 48px;
    color: #f19264;
  }

  .item__priceCourse {
    margin-bottom: 49px;
    background-color: #fff;
    padding: 56px 0 70px 0;
    border-radius: 10px;
    -moz-box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    -webkit-box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    box-shadow: 0 8px 23px 1px rgba(0,0,0,.07); /* drop shadow */
    h4 {
      font-size: 27px;
      font-family: PTSansCaptionBold;
      margin-left: 38px;
      margin-bottom: 12px;
    }
    .start__priceCourse {
      font-size: 24px;
      font-family: PTSans;
      margin-left: 38px;
      margin-bottom: 22px;
    }
    .count__priceCourse {
      font-size: 47px;
      font-family: PTSansCaptionBold;
      margin-left: 38px;
      margin-bottom: 43px;
    }
    .info__priceCourse {
      font-size: 25px;
      margin-left: 38px;
      font-family: PTSans;
      letter-spacing: -0.5px;
      margin-bottom: 4px;
    }
    .countSmall__priceCourse {
      font-size: 28px;
      font-family: PTSansCaptionBold;
      margin-left: 38px;
      margin-bottom: 100px;
    }
    a {
      margin-left: 30px;
      background-color: #f19264;
      padding: 16px 32px;
      font-size: 24px;
      color: #fff;
      border-radius: 8px;
      text-transform: uppercase;
      font-family: PTSansCaptionBold;
    }
  }
  .infoDocument__priceCourse {
    margin-left: 33px;
    font-size: 23px;
    font-family: PTSans;
    letter-spacing: 0.7px;
    color: #a6afb7;
  }
}

footer {
  padding-top: 72px;
  background-color: #709fff;
  padding-bottom: 72px;
  p {
    font-size: 29px;
    font-family: PTSans;
    line-height: 40px;
    display: inline-block;
    margin-left: 19px;
    letter-spacing: 0.5px;
    color: #fff;
  }
  a {
    font-size: 25px;
    text-transform: uppercase;
    font-family: PTSansCaptionBold;
    background-color: #fff;
    vertical-align: top;
    display: inline-block;
    padding: 25px 75px;
    margin: 24px 0 0 259px;
    line-height: 25px;
    border-radius: 10px;
    color: #474c50;
    &:hover {
      color: #474c50;
    }
  }
}
